import request from './request';
import config from './host';
import processKey from 'utils/processKey';
import getNews from 'news';
var BaseUrl = config.BaseUrl;
var historyApi = "".concat(BaseUrl, "/story-service/api/story/process");
var newsApi = "".concat(BaseUrl, "/process-service/api/process/task");
var resourceApi = "".concat(BaseUrl, "/resource-service/api/resource");
var authApi = "".concat(BaseUrl, "/auth-service/api");
export function getTags(payload) {
  return request.get("".concat(resourceApi, "/tag/searchTag?condition=").concat(payload.value));
}
export function getSources(payload) {
  return request.get("".concat(resourceApi, "/source/searchSource?condition=").concat(payload.value));
}
export function getNewsHistory(payload) {
  return request.get("".concat(historyApi, "/history/get/").concat(payload.value.id), payload.value.id);
}
export function getProperty(payload) {
  return request.get("".concat(resourceApi, "/property/searchProperty?condition=").concat(payload));
}
export function getAllSection() {
  return request.get("".concat(resourceApi, "/section/selectAllSection"));
} //获取评论员列表

export function getExpert(payload) {
  return request.get("".concat(resourceApi, "/expert/searchExpert?condition=").concat(payload));
}
export function createNewsProcess() {
  return request.get("".concat(newsApi, "/create?processKey=") + processKey.GENERAL_NEWS);
}
export function createNewsweiboOriginal() {
  return request.get("".concat(newsApi, "/create?processKey=") + processKey.WEIBO_ORIGINAL);
}
export function createLiveNews() {
  return request.get("".concat(newsApi, "/create?processKey=") + processKey.LIVE_NEWS);
}
export function createPictureNews() {
  return request.get("".concat(newsApi, "/create?processKey=") + processKey.PICTURE_NEWS);
}
export function createVideoNews() {
  return request.get("".concat(newsApi, "/create?processKey=") + processKey.VIDEO_NEWS);
}
export function getProcessPath(processKey) {
  return request.get("".concat(newsApi, "/create?processKey=") + processKey);
}
export function getTestProcessPath() {
  return new Promise(function (resolve) {
    setTimeout(function () {
      resolve({
        data: getNews()
      });
    }, 0);
  });
} //在线人数

export function getOnlineUsers() {
  return request.get("".concat(authApi, "/user/online"));
} //错误监控

export function getErrorList() {
  return request.get("http://localhost:3000/errorlist");
}