function getUrlParam(url, name) {
  var search = url.split('?')[1];
  if (!search) return;
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象

  var r = search.match(reg); //匹配目标参数

  if (r != null) return unescape(r[2]);
  return null; //返回参数值
}

export default getUrlParam;