function onBeforeLoadError() {
  if (!CGTN_ERROR_STORAGE) return; //检查是否存在资源错误缓存

  for (var i = 0; i < CGTN_ERROR_STORAGE.length; i++) {
    var error = CGTN_ERROR_STORAGE[i];
    if (!error || error === 'undefined') continue;
    var erorReport = new Errorlog(error);
    erorReport.resourceError();
  }
} //错误条目


function randomString(len) {
  len = len || 32;
  var chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';
  var maxPos = chars.length;
  var pwd = '';

  for (var i = 0; i < len; i++) {
    pwd += chars.charAt(Math.floor(Math.random() * maxPos));
  }

  return pwd;
} //通过message、colno与lineno进行相加计算阿斯克码值，可以生成错误的errorKey。
//错误事件，过滤重复的错误


function compressString(str, key) {
  var chars = 'ABCDEFGHJKMNPQRSTWXYZ';

  if (!str || !key) {
    return 'null';
  }

  var n = 0,
      m = 0;

  for (var i = 0; i < str.length; i++) {
    n += str[i].charCodeAt();
  }

  for (var j = 0; j < key.length; j++) {
    m += key[j].charCodeAt();
  }

  var num = n + '' + key[key.length - 1].charCodeAt() + m + str[str.length - 1].charCodeAt();

  if (num) {
    num = num + chars[num[num.length - 1]];
  }

  return num;
}

export default Errorlog;