export var INCREMENT = 'counter/INCREMENT';
export var DECREMENT = 'counter/DECREMENT';
export var RESET = 'counter/RESET';
export var INCREMENT_ASYNC = 'counter/INCREMENT_ASYNC';
export function increment() {
  return {
    type: INCREMENT
  };
}
export function decrement() {
  return {
    type: DECREMENT
  };
}
export function reset() {
  return {
    type: RESET
  };
}
export function onIncrementAsync() {
  return {
    type: INCREMENT_ASYNC
  };
}
