var BaseUrl, processKeySuffix, matchUrl, isEnv, defaultUpdateRole, newscenterBaseUrl, chartUrl, shortUrl, tagSuffix, uploadConfig;
var env = process.env.ENV;

switch (env) {
  case 'development':
    BaseUrl = 'https://bj-test-auth.cgtn.com';
    newscenterBaseUrl = 'https://newscenter-test-auth.cgtn.com';
    tagSuffix = 'harmony-audio-';
    uploadConfig = {
      section: 'cn_ed',
      domains: {
        img: 'https://newsedtest.cgtn.com',
        audio: 'https://newsedtest.cgtn.com',
        video: 'https://videoedtest.cgtn.com'
      }
    };
    break;

  case 'bundle':
    BaseUrl = routeConfig.cms.BaseUrl;
    newscenterBaseUrl = routeConfig.cms.newscenterBaseUrl;
    processKeySuffix = routeConfig.cms.processKeySuffix;
    matchUrl = routeConfig.cms.matchUrl;
    defaultUpdateRole = routeConfig.cms.defaultUpdateRole;
    chartUrl = routeConfig.cms.chartUrl;
    shortUrl = routeConfig.cms.shortUrl;
    tagSuffix = routeConfig.cms.tagSuffix;
    uploadConfig = routeConfig.uploadConfig;
    break;

  default:
    break;
}

export default {
  BaseUrl: BaseUrl,
  newscenterBaseUrl: newscenterBaseUrl,
  processKeySuffix: processKeySuffix,
  matchUrl: matchUrl,
  defaultUpdateRole: defaultUpdateRole,
  isEnv: isEnv,
  chartUrl: chartUrl,
  shortUrl: shortUrl,
  tagSuffix: tagSuffix,
  uploadConfig: uploadConfig
};